import React, {useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  Box,
  Image,
  Center,
  Heading,
  Text,
  SimpleGrid,
  Stack,
  Avatar,
} from '@chakra-ui/react';
import { getAlliances } from '../store/reducers/allianceSlice'
import AllianceComp from '../components/AllianceComp'

export default function Alliance() {
  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(getAlliances())
  }, [dispatch])
  const alliances = useSelector( state => state.alliances.allianceData)
  const singleAlliance = alliances!==undefined && alliances[0]
  console.log(singleAlliance)

 // console.log(alliances)
  return (
      <SimpleGrid spacing='40px' minChildWidth='260px'>
      { 
        alliances!==undefined && alliances.length>0 && alliances.map((item, index) =>(  <AllianceComp key={index} item={item} />
))
      }
      </SimpleGrid>
  );
}
