import * as securityTypes from './securityTypes'

function securityTypesInfo(security_status) {
  if(securityTypes.ONE_POINT_ZERO.LEVEL == security_status){
    return securityTypes.ONE_POINT_ZERO.HEX
  }

  if(securityTypes.ZERO_POINT_NINE.LEVEL == security_status)
  {
    return securityTypes.ZERO_POINT_NINE.HEX
  } 
  if(securityTypes.ZERO_POINT_EIGHT.LEVEL == security_status)
  {
    return securityTypes.ZERO_POINT_EIGHT.HEX
  } 
  if(securityTypes.ZERO_POINT_SEVEN.LEVEL == security_status)
  {
    return securityTypes.ZERO_POINT_SEVEN.HEX
  } 
  if(securityTypes.ZERO_POINT_SIX.LEVEL == security_status)
  {
    return securityTypes.ZERO_POINT_SIX.HEX
  } 
  if(securityTypes.ZERO_POINT_FIVE.LEVEL == security_status)
  {
    return securityTypes.ZERO_POINT_FIVE.HEX
  } 
  if(securityTypes.ZERO_POINT_FOUR.LEVEL == security_status)
  {
    return securityTypes.ZERO_POINT_FOUR.HEX
  } 
  if(securityTypes.ZERO_POINT_THREE.LEVEL == security_status)
  {
    return securityTypes.ZERO_POINT_THREE.HEX
  } 
  if(securityTypes.ZERO_POINT_TWO.LEVEL == security_status) {
    return securityTypes.ZERO_POINT_TWO.HEX
  }
  if(securityTypes.ZERO_POINT_ONE.LEVEL == security_status)
  {
    return securityTypes.ZERO_POINT_ONE.HEX
  } 
  if(securityTypes.ZERO_POINT_ZERO.LEVEL == security_status)
  {
    return securityTypes.ZERO_POINT_ZERO.HEX
  } 
  if(security_status < 0)
  {
    return securityTypes.ZERO_POINT_ZERO.HEX
  } 

}


export default securityTypesInfo
