import { createSlice } from '@reduxjs/toolkit'

export const allianceSlice = createSlice({
  name: 'alliance',
  initialState: {},
  reducers: {
    getAlliances() {},
    setAllianecs(state, action) {
      const allianceData = action.payload
      console.log(action.payload)
      return { ...state, allianceData }
    }
  }
})

export const { getAlliances, setAllianecs } = allianceSlice.actions
export default allianceSlice.reducer
