import  React, {Component} from 'react'
import axios from 'axios'

class fetchAPI extends Component {
  constructor(props) {
    super(props)
  }

  callAPI(path) {
    return axios.request({
      method: 'get',
      url: path
    }).then(res => res.data)
  }
}

const APIService = new fetchAPI
export default APIService
